<template>
	<div id="positionManagement">
		<el-table :data="tableData" style="width: 100%" height="650">
			<el-table-column label="职位" min-width="280" align="center">
				<template slot-scope="scope">
					<span v-for="(item,index) in scope.row.position_name" v-if="!scope.row.entry_type||scope.row.entry_type=='1'">{{item}}<span v-if="index==scope.row.position_name.length-1">(默认)</span></span>

					<span v-for="(item,index) in scope.row.position_name" v-else>{{item}}<span v-if="index==scope.row.arrRight.length">(默认)</span></span>
				</template>
			</el-table-column>
			<el-table-column label="权限" min-width="280" align="center">
				<template slot-scope="scope">
					<span v-for="(item,index) in scope.row.arrRight">{{ item }}<span v-if="index<scope.row.arrRight.length-1&&scope.row.is_admin!==0">,</span></span>
				</template>
			</el-table-column>
			<el-table-column label="操作" min-width="380" align="center">
				<template slot-scope="scope" v-if="scope.row.is_admin!==0">
					<router-link :to="{path:'/staff/editDuty',query:{infor:scope.row}}">
						<span size="small" type="text" class="blue">编辑
							</span>
					</router-link>
					<span size="small" type="text" @click="handleDelete(scope.row )" class="red">删除
						</span>
				</template>
			</el-table-column>
		</el-table>

	</div>
</template>

<script>
	import { staffDataGet } from "../api";
	import { treeMethods } from "../index";
	export default {
		data() {
			return {
				tableData: [{
					position_name: "管理员",
					arrRight: "全部",
					entry_type: 1,
					is_admin: 0
				}]
			};
		},
		created() {
			this.initTableList()
		},
		methods: {
			//	初始化表格数据
			initTableList() {
				let data = {
					get_position_list: 1
				}
				staffDataGet.getDutyList(data, (resp) => {
					this.tableData = this.tableData.concat(resp.data.position_list);
					this.tableData = treeMethods.isRight(this.tableData);
					console.log(this.tableData)
				})
			},
			//点击删除
			handleDelete(row) {
				console.log(row);
			}
		}
	};
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.blue {
		font-size: 14px;
		color: #4877E7;
	}
	
	.red {
		font-size: 14px;
		color: #E7487E;
	}
</style>


